	import util from "../../../utils/util.js";
 	import api from "../../../utils/api.js";
	export default {
		data() {
			return {
				title:'修改登录密码',
				isVerifyActive:false,
				isCommitActive:false,
				mobile:'',
				verify:'',
				password:'',
				codeText:'获取验证码',
				codeId:'00'
			};
		},
		onLoad(options) {
			console.log(options);
			if(options.index == 1){
				this.$data.title = '修改支付密码'
			}
		},
		methods:{
			getVerify(){
				if(this.$data.isVerifyActive){
					console.log('isVerifyActive');
					api.regSMS({
 						method:"POST",
 						data:{
 							mobile: this.$data.mobile
 						}
 					}).then((res)=>{
 						if(res.code == 0){
 							uni.showToast({
 								title: '短信发送成功',
 								icon:'none',
 								duration: 2000
 							});
							this.$data.codeId = res.data.id;
 							this.$data.codeText = "重新获取";
							console.log(res)
 						}
 					})
				}
			},
			commitPassword(){
				if(this.$data.isCommitActive){
					api.restpwd({
						method:"POST",
 						data:{
							id: this.$data.codeId,
 							mobile: this.$data.mobile,
							code: this.$data.verify,
							password:this.$data.passworld,
 						}
					}).then((res)=>{
						console.log(res)
 						if(res.code == 0){
							uni.showToast({
								title:'修改密码成功',
								icon:'success',
								duration:2000,
								success() {
									uni.navigateBack({
										delta:1
									})
								}
							})
						}
						
					})
				}
			},
			inputMobile(event){
				// console.log(this.$data.mobile);
				// console.log(util.vailPhone(this.$data.mobile));
				console.log(util.vailPhone(event.target.value));
				this.$data.mobile = event.target.value;
				util.vailPhone(event.target.value) ? this.$data.isVerifyActive = true : this.$data.isVerifyActive = false;
				this.judgeIsCommit();
			},
			inputVerify(event){
				this.$data.verify = event.target.value;
				this.judgeIsCommit();
			},
			inputPassword(event){
				this.$data.passworld = event.target.value;
				this.judgeIsCommit();
			},
			judgeIsCommit(){
				if(util.vailPhone(this.$data.mobile)&&this.$data.verify!=""&&this.$data.passworld!=""){
					this.$data.isCommitActive = true;
				}else{
					this.$data.isCommitActive = false;
				}
 			},
			
		}
	}
